import React, {useState} from 'react';
import './App.css';
import EuroDollarApp from "./EurosDollars/EuroDollarApp";
import {OnOff} from "./OnOff/OnOff";
import {Rating} from "./Rating/Rating";
import SqlConnect from "./SqlConnect/SqlConnect";
import {Practice} from "./Practice";


function App() {

    const [colapsed, setColapsed] = useState(false)
console.log(colapsed)
    return (
        <div className="App">
            <p>press to see colapsed components</p>
            <button onClick={()=>setColapsed(!colapsed)}>{colapsed?'wrap ':'unwrap'}</button>
            {colapsed && (
                <><EuroDollarApp /><hr/>
            <OnOff /><hr/>
            <Rating/><hr/>
            <SqlConnect /><hr/>
                </>
            )}
            <Practice/><hr/>
        </div>
    );
}

export default App;
